  font-size: 12px;
}
.navbar-ax .dropdown ul a:hover,
.navbar-ax .dropdown ul .active:hover,
.navbar-ax .dropdown ul li:hover>a {
  color: #222222;
}
.navbar-ax .dropdown:hover>ul {
  opacity: 1;
  top: 100%;
  visibility: visible;
}
.navbar-ax .dropdown .dropdown ul {
  top: 0;
  left: calc(100% - 30px);
  visibility: hidden;
}
.navbar-ax .dropdown .dropdown:hover>ul {
  opacity: 1;
  top: 0;
  left: 100%;
  visibility: visible;
}

